$backgroundColor: #000000;
$bodyColor: #ffffff;
$highlightColor: #ffcc00;
$bodyFont: "SF Mono", "Roboto Mono", "Fira Code", "Fira Mono", "Droid Sans Mono", "Source Code Pro", monospace;

@media (color-gamut: p3) {
  $backgroundColor: color(display-p3 0 0 0);
  $bodyColor: color(display-p3 1 1 1);
  $highlightColor: color(display-p3 1 0.8 0);
}

@mixin large {
  @media (min-width: 768px) {
    @content;
  }
}

@mixin medium {
  @media (min-width: 369px) {
    @content;
  }
}

@mixin small {
  @media (max-width: 368px) {
    @content;
  }
}

body {
  background: $backgroundColor;
  color: $bodyColor;
  font-family: $bodyFont;
  padding: 1rem;
}

.entry_text {
  line-height: 2;
}

.maintitle {
  margin-left: 2rem;
}

svg {
  stroke: $bodyColor;
  stroke-linecap: round;
  stroke-linejoin: bevel;
  stroke-width: 0.25em;
}

a {
  color: $bodyColor;
  text-decoration: none;
  font-weight: bold;
  &:hover {
    text-decoration: underline;
  }
}

.graphic_center {
  // display: block;
  margin-left: auto;
  margin-right: auto;
  height: 100%;

  @include large {
    width: 10%;
  }
  @include medium {
    width: 30%;
  }
  @include small {
    width: 50%;
  }

  max-width: 10rem;
}

.graphic_center svg {
  padding: 0 0.125em;
  &:hover {
    stroke: $highlightColor;
  }
  &:active {
    transform: scale(0.975);
    opacity: 0.9;
  }
  -webkit-transition-duration: 0.1s;
  transition-duration: 0.1s;
}

.graphic_center a span {
  display: none;
}